.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  background-color: #fff;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.logo {
  width: 150px;
  height: auto;
  margin-bottom: 10px;
}

.tagline {
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  color: #222;
}

.main-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.movie-listings {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  gap: 15px;
}

.movie-listings h2 {
  grid-column: 1 / -1;
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.footer {
  text-align: center;
  margin-top: 60px;
  font-size: 0.9rem;
  color: #666;
  border-top: 1px solid #ddd;
  padding-top: 15px;
}
